.statistics {
  min-width: 350px;
  min-height: 100px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.3);
  margin-top: 50px;
}
.title {
  margin: 30px 0;
  text-align: center;
  font-size: 36px;
  font-weight: 500;
  opacity: 0.7;
}
.stat_list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  justify-content: center;
}
.item {
  flex-direction: column;
  display: flex;
  align-items: center;
  min-width: 100px;
  padding: 15px;
  font-size: 24px;
}
.label {
  font-size: 24px;
}
.percentage {
  margin-top: 10px;
  font-size: 30px;
  font-weight: 500;
}
